import { IVPAIDElement } from "../core/IVPAIDElement.js";

export class Text extends IVPAIDElement {
  constructor(data, element, vpaidSettings) {
    super(data, element, vpaidSettings);
    this.markLoaded();
  }

  createElement() {
    let cacheKey = this.getDomCacheKey();
    let domElement = this.getFromDomCache(cacheKey);
    if(!domElement) {
      const rootDivElement = this.createRootElement();
      rootDivElement.innerText =
      this.element.content || "Lorem ipSum Lorem ipSum";
      rootDivElement.style.fontSize = `${this.element.fontSize}px`;
      rootDivElement.style.color = this.element.color || "black";
      rootDivElement.style.fontWeight = this.element.fontWeight || "normal";
      rootDivElement.style.backgroundColor =
        this.element.backgroundColor || "transparent";
      domElement = {root: rootDivElement, pIndices :[this.element.pIndex]};
      this.setDomCache(cacheKey, domElement);
    } else {
      domElement.pIndices.push(this.element.pIndex);
    }
  }
}
