import { cache } from "@babel/traverse";

export class IVPAIDElement {
  constructor(data, element, vpaidSettings) {
    this.data = data;
    this.element = element;
    this.vpaidSettings = vpaidSettings;
    this.createElement();
  }

  // Returns the HTML Element
  createElement() {
  }

  needsPauseResume() {
    return false;
  }

  needsVolume() {
    return false;
  }

  pause() { }

  resume() { }

  setVolume(volume) { }

  seek(newTime) { }

  getRootElement() {
    return this.domElement;
  }

  isPlaying(){
    return this.vpaidInstance.vpaidSettings.isPlaying;
  }

  createRootElement() {
    const divElement = document.createElement("div");
    divElement.id = "tm_vpaid_"+ this.element.type + "_" + this.element.dynamicId;
    divElement.style = {};
    divElement.style.position = "absolute";
    return divElement;
  }

  getComputedWidth() {
    return (this.element.width / this.data.outputResolution.width) * this.vpaidSettings.adWidth;
  }

  getComputedHeight() {
    return (this.element.height / this.data.outputResolution.height) * this.vpaidSettings.adHeight;
  }

  getComputedLeft() {
    return (this.element.x / this.data.outputResolution.width) * this.vpaidSettings.adWidth;
  }

  getComputedTop() {
    return (this.element.y / this.data.outputResolution.height) * this.vpaidSettings.adHeight;
  }

  applyStylesToRootElement(rootDivElement){
    rootDivElement.style.width = getComputedWidth() + "px";
    rootDivElement.style.height = getComputedHeight() + "px";
    rootDivElement.style.left = getComputedLeft() + "px";
    rootDivElement.style.top = getComputedTop() + "px";
    rootDivElement.style.display = "block";  
  }

  getParallelIndexForTime(seekTime) {
    let elapsedTime = 0;
  
    for (let i = 0; i < this.adParameters.body.sequence.length; i++) {
      const parallel = this.adParameters.body.sequence[i];
  
      // Check if the seekTime falls within the duration of the current parallel
      if (seekTime >= elapsedTime && seekTime < elapsedTime + parallel.duration) {
        return i; // Return the index of the parallel
      }
  
      elapsedTime += parallel.duration; // Move to next parallel duration
    }
  
    return this.adParameters.body.sequence.length - 1; // If seekTime exceeds total duration, return last parallel index
  }
  

  // Call this when the element has fully loaded its wresources
  markLoaded() {
    if (this.vpaidSettings.vpaidInstance) {
      this.vpaidSettings.vpaidInstance.markElementLoaded(this, this.element.id);
    }
  }

  getDomCacheKey() {
    return this.element.dynamicId + "" + this.element.type;
  }

  getFromDomCache(cacheKey) {
    return this.vpaidSettings.vpaidInstance.domCache.has(cacheKey);
  }

  setDomCache(cacheKey, domElement) {
    return this.vpaidSettings.vpaidInstance.domCache.set(cacheKey, domElement);
  }

  addToTimeline(adTimeline) {
    const self = this;
    const domElement = this.getFromDomCache(this.getDomCacheKey());
    let rootDivElement = domElement.root;
    
    // Sync Video Playback
    adTimeline.call(
      () => {
        self.applyStylesToRootElement(rootDivElement);
        self.vpaidSettings.adContainer.appendChild(rootDivElement);
      },
      null,
      self.element.start
    );

    adTimeline.call(
      () => {
        if(domElement.pIndices.has(self.element.pIndex + 1)) {
          self.adContainer.removeChild(rootDivElement);
        }
      },
      null,
      self.element.start + self.element.duration
    );
  }

  handleSeek(seekTime) {
    const domElement = this.getFromDomCache(this.getDomCacheKey());
    const rootDivElement = domElement.root;

    // if the current element at the seek time
    if (seekTime >= this.element.start && seekTime < this.element.start + this.element.duration) {
      if (!this.vpaidSettings.adContainer.has(rootDivElement)) {
        this.applyStylesToRootElement(rootDivElement);
        this.vpaidSettings.adContainer.appendChild(rootDivElement);
      }
    } else {
      if (this.vpaidSettings.adContainer.has(rootDivElement)) {// if the DOM element is same 
        const idx = this.getParallelIndexForTime(seekTime);
        if(!domElement.pIndices.has(idx)) {
          this.vpaidSettings.adContainer.removeChild(rootDivElement);
        }
      }
    }
  }
  
}
