import { IVPAIDElement } from "../core/IVPAIDElement.js";

export class Video extends IVPAIDElement {
  constructor(data, element, vpaidSettings) {
    super(data, element, vpaidSettings);
  }

  createElement() {
    let cacheKey = this.getDomCacheKey();
    let domElement = this.getFromDomCache(cacheKey);    
    if(!this.domElement) {
      const rootDivElement = this.createRootElement();
      const videoElement = document.createElement("video");
      videoElement.src = this.element.url;
      videoElement.style = {};
      videoElement.style.width = "100%";
      videoElement.style.height = "100%";
      videoElement.controls = false;
      videoElement.muted = this.element.muted || false;
      videoElement.preload = "auto";
      videoElement.onloadeddata = () => this.markLoaded();
      rootDivElement.appendChild(videoElement);
      domElement = {root: rootDivElement, video: videoElement, pIndices :[this.element.pIndex]};
      this.setDomCache(cacheKey, domElement);
    } else {
      domElement.pIndices.push(this.element.pIndex);
    }
  }

  addToTimeline(adTimeline) {
    const self = this;
    const domElement = this.getFromDomCache(this.getDomCacheKey());
    let rootDivElement = domElement.root;
    let videoElement = domElement.video;

    // Sync Video Playback
    adTimeline.call(
      () => {
        self.applyStylesToRootElement(rootDivElement);
        videoElement.currentTime = self.element.startOffset || 0;
        self.vpaidSettings.adContainer.appendChild(rootDivElement);
        videoElement.play();
      },
      null,
      self.element.start
    );

    adTimeline.call(
      () => {
        if(domElement.pIndices.has(self.element.pIndex + 1)) {
          videoElement.pause();
          self.adContainer.removeChild(rootDivElement);
        }
      },
      null,
      self.element.start + self.element.duration
    );
  }

  handleSeek(seekTime) {
    const domElement = this.getFromDomCache(this.getDomCacheKey());
    const rootDivElement = domElement.root;
    const videoElement = domElement.video;
    // if the current element at the seek time
    if (seekTime >= this.element.start && seekTime < this.element.start + this.element.duration) {
      let videoSeekTime = seekTime - this.element.start + (this.element.startOffset || 0);
      videoElement.currentTime = videoSeekTime;
      if (!this.vpaidSettings.adContainer.has(rootDivElement)) {
        this.applyStylesToRootElement(rootDivElement);
        this.vpaidSettings.adContainer.appendChild(rootDivElement);
      }
      if (this.isPlaying() && videoElement.paused) // if video is playing and we seeked to the frame which has this video element
          videoElement.play();
      else if(!this.isPlaying())
          videoElement.pause();
    } else {
      if (this.vpaidSettings.adContainer.has(rootDivElement)) {// if the DOM element is same 
        const idx = this.getParallelIndexForTime(seekTime);
        if(!domElement.pIndices.has(idx)) {
          this.vpaidSettings.adContainer.removeChild(rootDivElement);
          videoElement.pause();
        }
      }
    }
  }

  getVideoElement(){
    const domElement = this.getFromDomCache(this.getDomCacheKey());
    return domElement.video;
  }

  needsPauseResume() {
    return true;
  }

  needsVolume() {
    return true;
  }

  pause() {
    getVideoElement().pause();
  }

  resume() {
    getVideoElement().play();
  }

  setVolume(volume) {
    getVideoElement().volume = volume;
  }
}
