import { IVPAIDElement } from "../core/IVPAIDElement.js";
import QRCode from "qrcode";

export class QR extends IVPAIDElement {
  constructor(data, element, vpaidSettings) {
    super(data, element, vpaidSettings);
  }

  // Creates the QR code element inside the div container
  createElement() {

    let cacheKey = this.getDomCacheKey();
    let domElement = this.getFromDomCache(cacheKey);    
    if (!domElement) {
      const rootDivElement = this.createRootElement();
      const canvasElement = document.createElement("canvas");
      rootDivElement.appendChild(canvas);
      // Generate the QR code on the canvas
      QRCode.toCanvas(canvas, this.element.url, {
        width: this.getComputedWidth(),
        height: this.getComputedHeight()
      })
        .then(() => {
          this.markLoaded(); // Mark element as fully loaded
        })
        .catch((err) => {
          console.error("QR Code generation failed:", err);
        });
      domElement = { root: rootDivElement, canvas: canvasElement, pIndices: [this.element.pIndex] };
      this.setDomCache(cacheKey, domElement);
    } else {
      domElement.pIndices.push(this.element.pIndex);
    }
  }
}

