import { cache } from "@babel/traverse";
import { IVPAIDElement } from "../core/IVPAIDElement.js";

export class Image extends IVPAIDElement {
  constructor(data, element, vpaidSettings) {
    super(data, element, vpaidSettings);
  }

  createElement() {
    let cacheKey = this.getDomCacheKey();
    let domElement = this.getFromDomCache(cacheKey);
    if(!domElement) {
      const rootDivElement = this.createRootElement();
      const imageElement = document.createElement("img");
      imageElement.src = this.element.url;
      imageElement.style = {};
      imageElement.style.width = "100%";
      imageElement.style.height = "100%";
     // imageElement.style.objectFit = "contain"; // Ensure no distortion
      imageElement.style.display = "block"; // Prevents inline spacing issues
      imageElement.onload = () => this.markLoaded();
      rootDivElement.appendChild(imageElement);
      domElement = {root: rootDivElement, image: imageElement, pIndices :[this.element.pIndex]};
      this.setDomCache(cacheKey, domElement);
    } else {
      domElement.pIndices.push(this.element.pIndex);
    }
  }

  addToTimeline(adTimeline) {
    const self = this;
    const domElement = this.getFromDomCache(this.getDomCacheKey());
    let rootDivElement = domElement.root;
    
    // Sync Video Playback
    adTimeline.call(
      () => {
        self.applyStylesToRootElement(rootDivElement);
        self.vpaidSettings.adContainer.appendChild(rootDivElement);
      },
      null,
      self.element.start
    );

    adTimeline.call(
      () => {
        if(domElement.pIndices.has(self.element.pIndex + 1)) {
          self.adContainer.removeChild(rootDivElement);
        }
      },
      null,
      self.element.start + self.element.duration
    );
  }
}
